.experience {
  padding: 100px 20px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.glass-card {
  padding: 40px;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  color: white;
  max-width: 900px;
  width: 100%;
}

.experience h2 {
  font-size: 36px;
  margin-bottom: 30px;
  color: #00f7ff;
}

.experience-list {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.experience-item {
  background: rgba(255, 255, 255, 0.08);
  padding: 20px;
  border-radius: 12px;
  text-align: left;
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.experience-item h3 {
  margin: 0;
  font-size: 20px;
  color: #00f7ff;
}

.duration {
  font-style: italic;
  color: #aaa;
  font-size: 14px;
}
