.about {
  padding: 100px 20px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.glass-card {
  padding: 40px;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  color: white;
  max-width: 800px;
  width: 100%;
}

.about h2 {
  font-size: 36px;
  margin-bottom: 20px;
  color: #00f7ff;
}

.about p {
  font-size: 18px;
  color: #ddd;
  line-height: 1.6;
}
