
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: #0f0f0f;
  color: #ffffff;
  scroll-behavior: smooth;
}

section {
  padding: 80px 20px;
  max-width: 1100px;
  margin: auto;
}

h1, h2, h3 {
  font-weight: bold;
  text-align: center;
}

a {
  color: #61dafb;
  text-decoration: none;
}

body[data-theme='light'] a {
  color: #007acc;
}

body[data-theme='light'] h1,
body[data-theme='light'] h2,
body[data-theme='light'] h3 {
  color: #111;
}
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-size: cover;
  background-attachment: fixed;
  color: #ffffff;
  scroll-behavior: smooth;
}
body::before {
  content: '';
  position: fixed;
  top: 0; left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5); 
  z-index: -1;
}
.background-video {
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100vw;
  min-height: 100vh;
  object-fit: cover;
  z-index: -100;
  opacity: 0.5; 
  filter: blur(1.5px);
  pointer-events: none;
}